<template>
  <div ref="echartBar" class="echart-bar-performance" id="echart-bar-performance"></div>
</template>
<script>
// ECharts 
import echarts from "echarts/lib/echarts";
// 
import "echarts/lib/chart/bar";
// 
import "echarts/lib/component/tooltip";
export default {
  data() {
    return {
      myChart: null
    };
  },
  computed: {
    option: function() {
      return {
        color: ["#3398DB"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 
            type: "shadow" // 'line' | 'shadow'
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: "value"
          }
        ],
        series: [
          {
            name: "",
            type: "bar",
            barWidth: "60%",
            data: [10, 52, 200, 334, 390, 330, 220]
          }
        ]
      };
    }
  },
  mounted() {
    this.$nextTick(function() {
      this.myChart = echarts.init(this.$refs.echartBar);
      // console.log("mounted", this.myChart);
      this.myChart.setOption(this.option);
      this.$forceUpdate();
    });
  },
  watch: {
    // 
    width: function() {
      const width = document.getElementById("echart-bar-performance")
        .clientWidth;
      this.myChart.resize({ width });
    }
  }
};
</script>

<style scoped>
.echart-bar-performance {
  height: 100%;
  max-height: 400px;
  width: 100%;
  display: inline-block;
}
</style>



